import React from 'react';

const Error404 = () => (
  <div>
    <h1>Error 404</h1>
    <h2>Page not found...</h2>
    <p>
      Please check if the page you are trying to access really exists. if you
      have permission to acess this page.
    </p>
  </div>
);

export default Error404;



